.form {
    display: flex;
    column-gap: 12px;
}

.form__button {
    border: 0;
    border-radius: 6px;
    padding: 0 12px;
    background: var(--secondary-color);
}

.form__button:disabled {
    opacity: 0.6;
    cursor: not-allowed;
}

.form__button svg {
    margin-right: 6px;
}