.container {
    display: flex;
    justify-content: center;
    padding: 24px 24px 0;
}

.content {
    width: 100%;
    max-width: 700px;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    row-gap: 24px;
}

.title {
    font-size: 60px;
    text-align: center;
}
